.App {
  text-align: center;
}

header {
  background-color: #688fca;
  min-height: 25vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(231, 231, 231);
}

.App-link {
  color: #61dafb;
}

.productdata {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  margin: 1em;
  padding: 1em;
  text-align: left;
  background-color: rgba(243, 120, 233, 0.247);
}

.databackground {
  text-align: center;
  display: flex;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
  background-color: rgba(243, 162, 239, 0.247);
  border-radius: 10px;
  align-items: center;
}

.fontheader {
  font-family: 'Barlow', sans-serif;
  font-family: 'Luckiest Guy', cursive;
  font-size: 70px;
}

img {
  max-width: 400px;
}